  <div class="frontend" v-resize="onResize">
    <Layout class="layout-con">
            <sider  collapsible hide-trigger :collapsed-width="36" v-model="isCollapsed">
              <Menu theme="dark" width="auto" :class="menuitemClasses" @on-select="selectNavMenu">
                    <MenuItem name="search">
                      <Tooltip content="Search element" placement="right">
                        <Icon type="md-search"></Icon>
                      </Tooltip>
                    </MenuItem>
                    <MenuItem name="document">
                      <Tooltip content="Open Text Editor" placement="right">
                       <Icon type="md-list"></Icon>
                      </Tooltip>
                    </MenuItem>
                    <MenuItem name="refresh">
                      <Tooltip content="Reload Wxml file" placement="right">
                        <Icon type="md-open"></Icon>
                      </Tooltip>
                    </MenuItem>
                    <MenuItem name="debug" v-if="scene.debug">
                      <Tooltip content="Debug" placement="right">
                        <Icon type="md-create"></Icon>
                      </Tooltip>
                    </MenuItem>
                     <MenuItem name="about">
                      <Tooltip content="About" placement="right">
                        <Icon type="md-at"></Icon>
                      </Tooltip>
                    </MenuItem>
                </Menu>
            </sider>
            <Layout>
                <Content>
                  <div id="scene"></div>
                </Content>
            </Layout>
    </Layout>
    <div class="editor-button">
      <Tooltip placement="bottom" content="Zoom Out" :delay="1000">
        <Button shape="circle" icon="md-remove" @click="zoomOut"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Zoom In" :delay="1000">
        <Button shape="circle" icon="md-add" @click="zoomIn"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Reset Canvas" :delay="1000">
        <Button shape="circle" icon="md-refresh" @click="reset"></Button>
      </Tooltip>
    </div>
    
    <div class="editor-search" v-show="dialogs.search.visible">
        <Input type="text" v-model="dialogs.search.form.key" placeholder="Search" style="width:260px;"></Input>
        <span>{{ dialogs.search.form.status }}</span>
        <CheckboxGroup style="display:inline;" v-model="dialogs.search.form.types">
          <Checkbox v-for="item in dialogs.search.types" label="item.name">{{ item.title}}</Checkbox>
        </CheckboxGroup>
        <Button shape="circle" size="small" icon="md-arrow-back"></Button>
        <Button shape="circle" size="small" icon="md-arrow-forward"></Button>
        <Icon type="ios-close" size="24" color="rgba(255,255,255,.7)" @click="handleCloseSearch"/> 
    </div>
    <Drawer title="Script Editor" width="1024" :closable="false" v-model="scriptEditor.visible">
      <ButtonGroup class="code-button">
        <Button icon="md-create" @click="updateDebugScript">Update</Button>
      </ButtonGroup>
      <codemirror ref="code" v-model="scriptEditor.code" :options="scriptEditor.options"></codemirror>
    </Drawer>
    <Modal
        v-model="dialogs.about.visible"
        :styles="{top: '20px'}"
        title="About">
        <Row>
          <Col span="12">
            <h3>{{dialogs.about.form.title}}</h3>
            <p><Tag color="error">{{dialogs.about.form.version}}</Tag></p>
          </Col>
          <Col span="12">
            <p>{{dialogs.about.form.desc}}</p>
            <p> <Tag color="geekblue">Author</Tag>{{dialogs.about.form.author}}</p>
            <p><Tag color="geekblue">Email</Tag>{{dialogs.about.form.email}}</p>
          </Col>
       </Row>
        <div slot="footer">
            <Button type="success" size="large"  @click="dialogs.about.visible=false">OK</Button>
        </div>
    </Modal>
  </div>
</template>

