<script>
  import resize from 'vue-resize-directive'
  import Common from 'orion-common'

  import { codemirror } from 'vue-codemirror'
  import 'codemirror/mode/javascript/javascript'
  import 'codemirror/theme/monokai.css'
  import troll from './troll/index'
  import UILibs from './ui/zh-CN/index'

  const parse5 = require('parse5')
  const beautify = require('beautify')
  const crc32 = require('crc-32')

  export default {
    props: ["source"],
    directives: {
      resize,
    },
    components: {codemirror},
    data: function () {
      const validateName = (rule, value, callback) => {
        if (!value || value.length === 0) {
          callback(new Error('Please enter name'))
        } else {
          callback()
        }
      }

      return {
        split: {
          ratio: 0.3,
          oldRatio: 0.3,
          minWidth: 300,
          maxWidth: 600,
        },
        isCollapsed: true,
        vscodeExtMode: false,
        size: {
          width: 0,
          height: 0,
          scrollHeight: 0,
        },
        dialogs: {
          search: {
            visible: false,
            types: [
              {name: 'element', title: 'Element'},
              {name: 'property', title: 'Property'},
              {name: '#text', title: 'Text'},
              {name: '#comment', title: 'Comment'}
            ],
            form: {
              key: '',
              status: '0/0',
              types: ['element'],
              result: [],
              current: 0
            }
          },
          about: {
            visible: false,
            form: {
              version: 'v0.0.1',
              title: 'Outline For Wxml',
              desc: 'A visual preview for Weapp(.wxml)',
              author: 'unique',
              email: 'ods_hla@163.com'
            }
          }
        },
        components: {
          filters: [],
          wxmldefs: null,
          key: '',
          searchResult: []
        },
        scene: {
          stage: null,
          debug: false
        },
        scriptEditor: {
          document: null,
          code: '',
          codecrc: 0,
          options: {
            tabSize: 2,
            mode: 'text/javascript',
            theme: 'monokai',
            lineNumbers: true,
            line: true,
            matchBrackets: true,
            autofocus: true
          },
          visible: false
        }
      };
    },
    computed: {
      hasUndo: function () {
        return this.scene.stage ? this.scene.stage.hasUndo() : false
      },
      hasRedo: function () {
        return this.scene.stage ? this.scene.stage.hasRedo() : false
      },
      menuitemClasses: function () {
        return [
            'menu-item',
            this.isCollapsed ? 'collapsed-menu' : ''
        ]
      },
      splitMin: function () {
        return this.split.minWidth + 'px'
      },
      splitMax: function () {
        return this.split.maxWidth + 'px'
      }
    },
    methods: {
       onResize() {
        this.size.width = this.$el.clientWidth
        this.size.height = this.$el.clientHeight
        this.size.scrollHeight = this.size.height - 80
        this.scene.stage.resize(this.size.width, this.size.height)
      },
      zoomIn () {
        this.scene.stage.zoomIn()
      },
      zoomOut () {
        this.scene.stage.zoomOut()
      },
      reset () {
        this.scene.stage.reset()
      },
      loadScript (script) {
        if (script.length === 0) {
          this.scene.stage.clear()
          return
        }
        this.scene.stage.loadFromFile(script)
      },
      handleCloseSearch () {
        this.dialogs.search.visible = false
      },
      openBrowser (url) {
        window.__callVscode__({ cmd: 'openurl', info: url }, (data) => {
        })
      },
      openDocument () {
        window.__callVscode__({ cmd: 'opendoc' }, (data) => {
        })
      },
      readScript() {
        window.__callVscode__({ cmd: 'loadfile' }, (data) => {
          this.scriptEditor.code = data.result.code
          this.scriptEditor.codecrc = data.result.crc
          this.loadScript(`<root>${this.scriptEditor.code}</root>` || '')
        })
      },
      updateDebugScript () {
        let model = Common.Utils.common.trimString(this.scriptEditor.code)
        let code = `<root>${model}</root>`
        this.loadScript(code)
        this.scriptEditor.visible = false
      },
      searchComponents () {
        this.components.searchResult = UILibs.search({
          key: this.components.key,
          filters: this.components.filters
        })
      },
      selectNavMenu (name) {
        if (name === 'search') {
          this.dialogs.search.visible = !this.dialogs.search.visible;
        } else if (name === 'document') {
          this.openDocument()
        } else if (name === 'refresh') {
          this.readScript()
        } else if (name === 'debug') {
          this.scriptEditor.visible = true
          this.$nextTick(() => {
            this.$refs['code'].refresh()
          })
        } else if (name === 'about') {
          this.dialogs.about.visible = true
        }
      },
      test(){
        const code = '<import src="item.wxml"/><view class="container"><view class="title" bindtap="tap" wx:if="{{condition}}"><text>听写</text><!-- 注视说明， 测试 --></view><text class="progress-text">{{progressText}}</text><progress percent="{{progressPercent}}" stroke-width="12" border-radius="6" show-info="{{true}}"/></view>'
        this.scriptEditor.code = beautify(code,{format: 'xml', indent_size: 2})
      }
    },
    created: function () {
      this.components.libs = UILibs.getCategories()
      for(let lib of this.components.libs){
        this.components.filters.push(lib.category)
      }

      this.dialogs.editElement.form.current = this.dialogs.editElement.form.empty
      this.components.wxmldefs = UILibs.wxmlDefs()
      this.vscodeExtMode = window.__vscodeExtMode__
    },
    mounted: function () {
      this.size.width = this.$el.clientWidth
      this.size.height = this.$el.clientHeight
      this.size.scrollHeight = this.size.height - 80

      this.scene.stage = troll.init({
        container: 'scene',
        width: this.size.width,
        height: this.size.height,
        debug: false,
        readonly: true,
        events: {
          select: (elem) => {
          },
          jump: (location) => {
            // 跳转到指定文本
            this.$nextTick(() => {
              window.__callVscode__({ cmd: 'opendoc', info: location }, (data) => {})
            })
          }
        }
      })

      this.test()
      this.readScript()
    }
  };
</script>
